import { MissingBillingAddress, MissingCart } from '@/checkout/api/errors';
import { setBillingAddressMutation } from '@/checkout/api/setBillingAddress/graphql/setBillingAddress.graphql';
import { dispatchApiCall } from '@/checkout/api/utils/dispatchApiCall';
import { transformCart } from '@/checkout/data/transforms/index';

export interface BillingAddressInput {
  address?: {
    city: string;
    company?: string;
    country_code: string;
    custom_attributes?: {
      attribute_code: string;
      value?: string;
      selected_options?: { uid: string; value: string }[];
    }[];
    firstname: string;
    lastname: string;
    postcode?: string;
    region?: string;
    region_id?: number;
    save_in_address_book?: boolean;
    street: string[];
    telephone?: string;
    vat_id?: string;
  };
  customer_address_id?: number;
  same_as_shipping?: boolean;
  use_for_shipping?: boolean;
}

export const setBillingAddress = async ({
  signal,
  cartId,
  input,
}: {
  signal?: AbortSignal;
  cartId: string;
  input: BillingAddressInput;
}) => {
  const { address, same_as_shipping } = input;

  if (!cartId) throw new MissingCart();
  if (!same_as_shipping && !address) throw new MissingBillingAddress();

  return await dispatchApiCall({
    type: 'mutation',
    query: setBillingAddressMutation,
    options: { signal, variables: { cartId, input } },
    path: 'setBillingAddressOnCart.cart',
    signalType: 'cart',
    transformer: transformCart,
  });
};
