import {
  CHECKOUT_DATA_FRAGMENT,
  CART_SUMMARY_FRAGMENT,
} from '@/checkout/api/getCheckoutData/graphql/getCheckoutData.graphql';

export const setBillingAddressMutation = /* GraphQL */ `
  mutation setBillingAddress(
    $cartId: String!
    $input: BillingAddressInput!
  ) {
    setBillingAddressOnCart(
      input: { cart_id: $cartId, billing_address: $input }
    ) {
      cart {
        id
        ...CheckoutData
        ...CartSummaryItems
      }
    }
  }
  ${CHECKOUT_DATA_FRAGMENT}
  ${CART_SUMMARY_FRAGMENT}
`;
